
'use client';

import { useState, useEffect, useCallback } from 'react';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Star } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { usePageBackground } from '@/hooks/usePagebackground';
import { Badge } from '@/components/ui/badge';
import { useRouter } from 'next/navigation';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';

const RazorpaySubscriptionButton = ({ userId, planId, onSubscriptionSuccess }: { userId: string, planId: string, onSubscriptionSuccess: () => void }) => {
  const [error, setError] = useState<string | null>(null);
  const formId = `razorpay-form-${planId}`;

  useEffect(() => {
    // This effect should only run once on the client-side when the component mounts.
    if (typeof window === 'undefined') {
      return;
    }

    const handleRazorpaySuccess = (event: any) => {
      // Custom logic to check if the payment was for this user
      if (event.detail?.subscription_id) {
         console.log('razorpay.subscription.subscribed event caught', event.detail);
         onSubscriptionSuccess();
      }
    };
    
    // Using a more specific event listener tied to the window
    window.addEventListener('razorpay.subscription.subscribed', handleRazorpaySuccess);

    try {
      const rzpForm = document.getElementById(formId);
      if (!rzpForm) {
        throw new Error('Razorpay form container not found.');
      }
      
      // Ensure the container is empty before appending the new script
      while (rzpForm.firstChild) {
        rzpForm.removeChild(rzpForm.firstChild);
      }

      const script = document.createElement('script');
      script.src = 'https://cdn.razorpay.com/static/widget/subscription-button.js';
      script.async = true;

      // **CRITICAL FIX**: `data-subscription-buttonid` is the correct attribute.
      script.setAttribute('data-subscription-buttonid', planId); 
      script.setAttribute('data-button_theme', 'brand-color');

      // **CRITICAL FIX**: Pass the Firebase user ID in the notes
      script.setAttribute('data-notes.user_id', userId);

      script.onerror = () => {
        console.error("Razorpay script failed to load.");
        setError('Failed to load the payment script. Please check your network connection and try again.');
      };

      rzpForm.appendChild(script);

    } catch (e: any) {
      console.error("Razorpay script injection error:", e);
      setError(e.message || 'An unexpected error occurred while loading the payment button.');
    }

    // Cleanup listener when component unmounts
    return () => {
      window.removeEventListener('razorpay.subscription.subscribed', handleRazorpaySuccess);
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [planId, userId]); // Dependency array ensures this runs only when planId or userId changes.

  if (error) {
    return <p className="text-center text-destructive text-sm font-medium">{error}</p>;
  }

  return <form id={formId} className="w-full flex justify-center"></form>;
};


export default function PricingPage() {
  usePageBackground('');
  const { user, profile, isLoading, fetchProfile } = useAuth();
  const router = useRouter();
  const { toast } = useToast();
  
  // This is the Subscription BUTTON ID, not the Plan ID
  const RAZORPAY_BUTTON_ID = 'pl_RDsiJqCv0SUHNV'; 

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
    }
  }, [isLoading, user, router]);

  const handleSubscriptionSuccess = useCallback(async () => {
    if (!user) return;
    toast({
      title: "Payment Successful!",
      description: "Welcome to Pro! Your account is being upgraded. This may take a moment to reflect.",
    });

    let attempts = 0;
    const maxAttempts = 10;
    
    const pollProfile = async () => {
        attempts++;
        try {
            console.log(`Polling for profile update, attempt ${attempts}`);
            const updatedProfile = await fetchProfile(user);
            if (updatedProfile?.subscriptionStatus === 'pro') {
                console.log("Profile updated to Pro!");
                toast({
                    title: "Upgrade Complete!",
                    description: "You now have full access to Abacus Pro.",
                });
                // No need to continue polling
            } else if (attempts < maxAttempts) {
                setTimeout(pollProfile, 3000); // Poll again after 3 seconds
            } else {
                console.warn("Max polling attempts reached. UI might not be updated.");
                toast({
                    title: "Upgrade Processing",
                    description: "Your upgrade is processing and will be reflected shortly. Please refresh if you don't see the change.",
                    variant: "default",
                });
            }
        } catch (error) {
            console.error("Error polling for profile:", error);
            if (attempts < maxAttempts) {
                setTimeout(pollProfile, 3000);
            }
        }
    };
    
    setTimeout(pollProfile, 3000); // Start first poll after 3 seconds

  }, [user, fetchProfile, toast]);

  if (isLoading || !user || !profile) {
    return (
        <div className="max-w-4xl mx-auto">
            <div className="text-center mb-12">
                <Skeleton className="h-12 w-3/4 mx-auto" />
                <Skeleton className="h-6 w-1/2 mx-auto mt-4" />
            </div>
            <div className="flex justify-center px-4 sm:px-0">
                <Skeleton className="h-[500px] w-full max-w-md" />
            </div>
      </div>
    );
  }

  const isAlreadyPro = profile?.subscriptionStatus === 'pro';

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-foreground font-headline sm:text-5xl">
          Unlock Your Full Potential
        </h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-muted-foreground">
          Choose the plan that's right for you and start mastering mental math today.
        </p>
      </div>
      
      <div className="flex justify-center px-4 sm:px-0">
        <Card className="w-full max-w-md shadow-2xl border-2 border-primary md:scale-105">
           <CardHeader className="text-center bg-primary/10 p-6">
                <Star className="w-12 h-12 text-primary mx-auto mb-4" />
                <CardTitle className="text-3xl font-bold">Abacus Pro</CardTitle>
                <CardDescription className="text-lg">
                    Everything you need to become a math whiz.
                </CardDescription>
                <div className="mt-4 flex flex-col items-center gap-2">
                  <div className="flex items-baseline gap-4">
                    <p className="text-2xl font-semibold text-muted-foreground line-through">₹799</p>
                    <Badge variant="destructive" className="text-base h-fit py-1 px-3 -rotate-12 transform">55% OFF</Badge>
                  </div>
                  <p className="text-5xl font-extrabold tracking-tight text-foreground">₹360<span className="text-base font-medium text-muted-foreground">/month</span></p>
                </div>
           </CardHeader>
           <CardContent className="p-6 md:p-8">
                <ul className="space-y-4">
                    {includedFeatures.map((feature) => (
                        <li key={feature} className="flex items-center gap-3">
                            <Check className="h-6 w-6 text-green-500" />
                            <span className="text-muted-foreground">{feature}</span>
                        </li>
                    ))}
                </ul>
           </CardContent>
           <CardFooter>
             {isAlreadyPro ? (
                <p className="w-full text-center text-primary font-semibold">You are already a Pro member!</p>
             ) : (
                <RazorpaySubscriptionButton 
                  userId={user.uid}
                  planId={RAZORPAY_BUTTON_ID}
                  onSubscriptionSuccess={handleSubscriptionSuccess}
                />
             )}
           </CardFooter>
        </Card>
      </div>
    </div>
  )
}

const includedFeatures = [
  'Unlimited Practice Tests',
  'All Difficulty Levels',
  'Detailed Progress Tracking',
  'No Advertisements',
  'Priority Support',
];

